import { createAsyncThunk } from '@reduxjs/toolkit';
import db from '../../utils/firebase';

export const getPhrases = createAsyncThunk(
  'phrase/getList',
  async (options = {}, { rejectWithValue }) => {
    try {
      let queryBuilder = await db.collection('phrases');
      if (
        options.isFilter &&
        Array.isArray(options.filteredCategories) &&
        options.filteredCategories.length
      ) {
        queryBuilder = queryBuilder.where(
          'categories',
          'array-contains-any',
          options.filteredCategories,
        );
      }
      queryBuilder = queryBuilder.orderBy('createdAt', 'desc');
      if (+options.limit !== -1) {
        queryBuilder = queryBuilder.limit(options.limit || 100);
      }
      return queryBuilder
        .get()
        .then((querySnapshot) =>
          querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id })),
        );
    } catch (err) {
      return rejectWithValue(err);
    }
  },
);

export const addPhrase = createAsyncThunk(
  'phrase/create',
  async (data, { rejectWithValue }) => {
    try {
      const phrase = await db.collection('phrases').add({
        ...data,
        createdAt: new Date().toISOString(),
      });
      const res = await phrase.get();
      return {
        id: phrase.id,
        ...res.data(),
      };
    } catch (err) {
      return rejectWithValue(err);
    }
  },
);

export const updatePhrase = createAsyncThunk(
  'phrase/update',
  async (data, { rejectWithValue }) => {
    try {
      await db.collection('phrases').doc(data.id).set(data, { merge: true });
      return data.id;
    } catch (err) {
      return rejectWithValue(err);
    }
  },
);

export const removePhrase = createAsyncThunk(
  'phrase/remove',
  async (phraseId, { rejectWithValue }) => {
    try {
      await db.collection('phrases').doc(phraseId).delete();
      return phraseId;
    } catch (err) {
      return rejectWithValue(err);
    }
  },
);
