/* eslint no-param-reassign: "off" */

import { createSlice } from '@reduxjs/toolkit';
import {
  addPhrase,
  getPhrases,
  updatePhrase,
  removePhrase,
} from './phraseService';

export const phraseSlice = createSlice({
  name: 'phrase',
  initialState: {
    loading: 'idle',
    items: [],
    filteredCategories:
      JSON.parse(localStorage.getItem('filter-categories')) || [],
    currentRequestId: undefined,
    error: null,
  },
  reducers: {
    setFilteredCategories: (state, action) => {
      localStorage.setItem('filter-categories', JSON.stringify(action.payload));
      state.filteredCategories = action.payload;
    },
  },
  extraReducers: {
    [addPhrase.pending]: (state, action) => {
      if (state.loading === 'idle') {
        state.loading = 'pending';
        state.currentRequestId = action.meta.requestId;
      }
    },
    [addPhrase.fulfilled]: (state, action) => {
      const { requestId } = action.meta;
      if (state.loading === 'pending' && state.currentRequestId === requestId) {
        state.loading = 'idle';
        state.items.unshift(action.payload);
        state.currentRequestId = undefined;
      }
    },
    [addPhrase.rejected]: (state, action) => {
      const { requestId } = action.meta;
      if (state.loading === 'pending' && state.currentRequestId === requestId) {
        state.loading = 'idle';
        state.error = action.error;
        state.currentRequestId = undefined;
      }
    },
    [getPhrases.pending]: (state, action) => {
      if (state.loading === 'idle') {
        state.loading = 'pending';
        state.currentRequestId = action.meta.requestId;
      }
    },
    [getPhrases.fulfilled]: (state, action) => {
      const { requestId } = action.meta;
      if (state.loading === 'pending' && state.currentRequestId === requestId) {
        state.loading = 'idle';
        state.items.splice(0);
        state.items.push(...action.payload);
        state.currentRequestId = undefined;
      }
    },
    [getPhrases.rejected]: (state, action) => {
      const { requestId } = action.meta;
      if (state.loading === 'pending' && state.currentRequestId === requestId) {
        state.loading = 'idle';
        state.error = action.error;
        state.currentRequestId = undefined;
      }
    },
    [updatePhrase.fulfilled]: (state, action) => {
      state.items = state.items.filter(
        (item) => item.id !== action.payload.phraseId,
      );
    },
    [removePhrase.fulfilled]: (state, action) => {
      state.items = state.items.filter(
        (item) => item.id !== action.payload.phraseId,
      );
    },
  },
});

export const { setFilteredCategories } = phraseSlice.actions;

export const selectPhrases = (state) => state.phrase.items;
export const selectFilteredCategories = (state) =>
  state.phrase.filteredCategories;

export default phraseSlice.reducer;
